.containerNeon {
  text-align: center;
  font-family: var(--font-tertiary);
  max-height: 100%;
  margin-bottom: 300px;
}

h1 {
  margin: 1em;
  color: white;
  font-size: 10vw;
  text-shadow: 0 0 5px #fff, 0 0 10px #fff, 0 0 15px #dd35b9, 0 0 17px #dd35b9,
    0 0 20px #dd35b9, 0 0 25px #dd35b9, 0 0 30px #dd35b9;
}

.fantastic {
  margin-top: 1em;
  animation: fantastic 1s infinite alternate-reverse;
}

@keyframes fantastic {
  0% {
    text-shadow: 0 0 5px #fff, 0 0 10px #fff, 0 0 15px #dd35b9, 0 0 17px #dd35b9,
      0 0 20px #dd35b9, 0 0 25px #dd35b9, 0 0 30px #dd35b9;

    font-style: normal;
  }

  20%,
  22%,
  25%,
  27%,
  35%,
  50%,
  52%,
  65%,
  100% {
    text-shadow: 0 0 10px #fff, 0 0 20px #fff, 0 0 25px #dd35b9,
      0 0 27px #dd35b9, 0 0 30px #dd35b9, 0 0 35px #dd35b9, 0 0 40px #dd35b9;

    font-style: normal;
  }

  21%,
  26%,
  51% {
    text-shadow: none;
    font-style: italic;
  }
}

@media screen and (prefers-reduced-motion: reduce) {
  .fantastic {
    animation: none;
  }
}

@media screen and (max-width: 600px) {
  .containerNeon {
    max-height: 50%;
    margin-top: 50%;
  }
}
